import {Component,Input} from "@angular/core";
import {Product} from "../Product.model";

@Component({
	selector:"product-price",
	template:`
		<div class="price-display">\${{price}}</div>
	`
})

export class ProductPriceComponent{
	@Input() price : number;
}


